import React, { useState } from "react";
import { BrowserRouter as Router, Route, Routes, Link } from "react-router-dom";
import { Layout, Menu } from "antd";
import "antd/dist/antd.css";
import {
  HomeOutlined,
  LoginOutlined,
  UserAddOutlined,
} from "@ant-design/icons";
import HomePage from "./pages/Home";
import DashboardPage from "./pages/Dashboard";
import LoginPage from "./pages/LoginPage";
import RegistrationPage from "./pages/RegistrationPage";
import CoursesPage from "./pages/CoursesPage";
import ClassPage from "./components/ClassPage"; // Import the ClassPage component
import AppHeader from "./components/Header";

const { Header, Content, Footer } = Layout;

const App: React.FC = () => {
  const [jwtToken, setJwtToken] = useState<string | null>(null);

  const handleLogin = (token: string) => {
    setJwtToken(token);
  };

  const handleLogout = () => {
    setJwtToken(null);
  };

  return (
    <Router>
      <Layout>
        <AppHeader jwtToken={jwtToken} onLogout={handleLogout} />
        {/* <Menu theme="dark" mode="horizontal" defaultSelectedKeys={["home"]}>
            <Menu.Item key="home" icon={<HomeOutlined />}>
              <Link to="/">Home</Link>
            </Menu.Item>
            <Menu.Item key="login" icon={<LoginOutlined />}>
              <Link to="/login">Login</Link>
            </Menu.Item>
            <Menu.Item key="register" icon={<UserAddOutlined />}>
              <Link to="/register">Register</Link>
            </Menu.Item>
            <Menu.Item key="courses">
              <Link to="/courses">Courses</Link>
            </Menu.Item>
          </Menu>
        </AppHeader> */}
        <Content style={{ padding: "50px 50px" }}>
          <div className="site-layout-content">
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route
                path="/login"
                element={<LoginPage onLogin={handleLogin} />}
              />
              <Route path="/register" element={<RegistrationPage />} />
              <Route path="/dashboard" element={<DashboardPage />} />
              {/* <Route path="/courses" element={<CoursesPage />} /> */}
              {jwtToken && <Route path="/courses" element={<CoursesPage />} />}
              <Route path="/class/:classId" element={<ClassPage />} />{" "}
              {/* Add the route for ClassPage */}
            </Routes>
          </div>
        </Content>
        <Footer style={{ textAlign: "center" }}>
          Your App ©{new Date().getFullYear()}
        </Footer>
      </Layout>
    </Router>
  );
};

export default App;
