// client/src/components/ClassPage.tsx

import React from "react";
import { useParams } from "react-router-dom";
import { Card, Typography } from "antd";

const { Title, Text } = Typography;

interface Class {
  id: number;
  title: string;
  description: string;
  videoUrl: string;
}

const classData: Class[] = [
  {
    id: 1,
    title: "Class-1A",
    description: "This is the description of Class 1A",
    videoUrl: "https://example.com/class1A-video",
  },
  {
    id: 2,
    title: "Class-1B",
    description: "This is the description of Class 1B",
    videoUrl: "https://example.com/class1B-video",
  },
  // Add more class data as needed
];

const ClassPage: React.FC = () => {
  const { classId } = useParams<{ classId: string }>();
  const selectedClass = classData.find((cls) => cls.title === classId);
  console.log(selectedClass);
  if (!selectedClass) {
    return <div>Class not found.</div>;
  }

  return (
    <div>
      <Card title={selectedClass.title}>
        <Text>{selectedClass.description}</Text>
        <Title level={5}>Video:</Title>
        <iframe
          title={selectedClass.title}
          width="560"
          height="315"
          src={selectedClass.videoUrl}
          frameBorder="0"
          allowFullScreen
        ></iframe>
      </Card>
    </div>
  );
};

export default ClassPage;
