// client/src/pages/CoursesPage.tsx

import React from "react";
import { List } from "antd";
import CourseItem from "../components/CourseItem"; // Import the CourseItem component

interface Course {
  id: number;
  title: string;
  description: string;
  classes: string[];
}

const coursesData: Course[] = [
  {
    id: 1,
    title: "Course 1",
    description: "This is the description of Course 1",
    classes: ["Class-1A", "Class-1B", "Class-1C"],
  },
  {
    id: 2,
    title: "Course 2",
    description: "This is the description of Course 2",
    classes: ["Class-2A", "Class-2B", "Class-2C"],
  },
  // Add more course data as needed
];

const CoursesPage: React.FC = () => {
  return (
    <div>
      <h1>List of Courses</h1>
      <List
        grid={{ gutter: 16, xs: 1, sm: 2, md: 3, lg: 4, xl: 4, xxl: 4 }}
        dataSource={coursesData}
        renderItem={(course) => <CourseItem course={course} />} // Use the CourseItem component
      />
    </div>
  );
};

export default CoursesPage;
